<template>
  <div class="container pt-10 mx-auto">
    <div
      class="px-2 py-5 m-2 mt-10 text-center rounded-md shadow-lg  md:m-auto md:w-1/2 bg-gray-50"
    >
      <vee-form :validation-schema="schema" @submit="passwordReset" class="">
        <div class="mb-3">
          <vee-field
            type="email"
            name="email"
            class="form-control"
            placeholder="Enter Email"
          />
          <ErrorMessage class="text-red-600" name="email" />
        </div>
        <button class="px-3 py-2 text-white bg-blue-500">
          Send Reset Email
        </button>
      </vee-form>
    </div>
  </div>
</template>

<script>
import { sendPasswordResetEmail } from "firebase/auth";
import { auth } from "@/plugins/firebase";
export default {
  data() {
    return {
      schema: {
        email: "required",
      },
      buttonText: "Send Reset Email",
    };
  },
  methods: {
    passwordReset(values) {
      this.buttonText = "Sending...";
      sendPasswordResetEmail(auth, values.email)
        .then(() => {
          this.buttonText = "Resent Email sent.";
          setTimeout(() => {
            this.$router.replace("/login");
          }, 2000);
        })
        .catch((error) => {
          console.log(error);
        });
    },
    showAlert() {
      this.$swal("Email sent!", "Check your inbox and spam.", "success");
    },
  },
};
</script>

<style lang="scss" scoped>
</style>